import {
  getElementById,
  addEventListener,
  elementHasClass,
  addClassToElement,
  removeClassFromElement,
  removeClassFromElements,
  getChildElements,
  getHtmlElmentsByClass,
} from "./ui-scripts";

export const toggleFilter = () => {
  const selectedlabel = getHtmlElmentsByClass("c-sp-filters__fil-hwrap");
  const selectedlabeltxt = getHtmlElmentsByClass("-reset-filter");
  selectedlabel.map((element) => {
    // console.log(element);
    addEventListener(element, "click", () => {
      let filterParent = element.closest('.c-sp-filters__wrap');
      let filterComp = filterParent.querySelector('.c-sp-filters__catg-wrap');
      const filterCont = filterComp.parentElement.parentElement.querySelector('.c-sp-filters__content');
      const filterCatg = filterComp.parentElement.parentElement.querySelector('.c-sp-filters__catogories');
      let resetWrap = filterParent.querySelector('.c-sp-filters__reset-wrap');
      const checkBoxList = [...filterParent.querySelectorAll('.c-sp-filters__catg-checkbox')];

      if (filterComp.classList.contains("-active")) {
        removeClassFromElement(filterComp, '-active');
        filterCont && filterCont.classList.add('-fixcont');
        filterCatg && filterCatg.classList.add('-flexcatg');
        element && element.classList.remove('-active');
        const isChecked = checkBoxList.some(check => (check.checked === true));
        isChecked ? (!elementHasClass(resetWrap, '-active') && addClassToElement(resetWrap, '-active')) :
          (elementHasClass(resetWrap, '-active') && removeClassFromElement(resetWrap, '-active'));
      } else {
        addClassToElement(filterComp, '-active');
        filterCont && removeClassFromElement(filterCont, '-fixcont');
        filterCatg && removeClassFromElement(filterCatg, '-flexcatg');
        element && element.classList.add('-active');
      }
    });
  });

  // handle reset
  selectedlabeltxt.map((element) => {
    addEventListener(element, "click", () => {
      // let filterbtn = element.parentElement.parentElement.querySelector('.c-sp-filters__filbtn');
      let filterParent = element.closest('.c-sp-filters__wrap');
      let filterComp = filterParent.querySelector('.c-sp-filters__catg-wrap');
      const filterCont = filterComp.parentElement.parentElement.querySelector('.c-sp-filters__content');
      const filterCatg = filterComp.parentElement.parentElement.querySelector('.c-sp-filters__catogories');
      const filterToggle = filterParent.querySelector('.c-mindx-filters__fil-hwrap');
      const resetWrap = element.closest('.c-sp-filters__reset-wrap')

      if (filterToggle && filterToggle.classList.contains("-active")) {
        filterToggle.classList.remove('-active');
      }

      if (filterComp && element) {
        removeClassFromElement(filterComp, '-active');
        filterCont && filterCont.classList.add('-fixcont');
        filterCatg && filterCatg.classList.add('-flexcatg');

        const checkBoxList = [...filterParent.querySelectorAll('.c-sp-filters__catg-checkbox')];
        const labelList = [...filterParent.querySelectorAll('.c-sp-filters__label-text')];
        labelList.length > 0 && labelList.forEach(ele => {
          ele && elementHasClass(ele, '-active') && removeClassFromElement(ele, '-active');
        });
        (checkBoxList.length > 0) && checkBoxList.forEach(item => {
          (item.checked && (item.checked = false))
          // item.parentElement.nextElementSibling.classList.remove('-active');
        });
        let filterTextUpdate = filterParent.querySelector('.c-sp-filters__text');
        filterTextUpdate.innerText = `Filter (0)`;
        const countTag = [...filterParent.querySelectorAll('.c-sp-filters__catgtitle-count')];
        countTag.length > 0 && countTag.forEach(ele => {
          !elementHasClass(ele, '-hidecount') && addClassToElement(ele, '-hidecount');
        })

        resetWrap && elementHasClass(resetWrap, '-active') && removeClassFromElement(resetWrap, '-active')
      }
    });
  });

  // handle checkbox click
  const checkContainer = getHtmlElmentsByClass("c-sp-filters__catg-checkbox");
  const clearBtn = getHtmlElmentsByClass("c-sp-filters__catgtitle-clr");

  clearBtn.map((clr) => {
    addEventListener(clr, "click", (btn) => {
      const parent = btn.target.closest('.c-sp-filters__catgtitle-count');
      const idCont = parent.querySelector('.c-sp-filters__catgtitle-num');
      const hasId = idCont && idCont.id;
      if (hasId) {
        let checkBoxCount = document.querySelectorAll(`input[name=${hasId}]:checked`);
        checkBoxCount.forEach((el) => {
          el.checked = false;
        })
      }
      parent.classList.add('-hidecount');
    })
  });

  checkContainer.map((elem) => {
    addEventListener(elem, "click", (el) => {
      const countBox = document.getElementById(el.target.name);
      const textContainer = countBox && countBox.closest('.c-sp-filters__catgtitle-count');
      const boxContainer = el.target.closest('.c-sp-filters__catg-list');
      const itemContainer = el.target.closest('.c-sp-filters__label');
      const itemText = itemContainer && itemContainer.querySelector('.c-sp-filters__label-text');
      const allCheckBox = boxContainer.querySelectorAll(`input[name=${el.target.name}]`);
      const parentEle = el.target.closest('.c-sp-filters__wrap');
      // handle select all
      if (itemText) {
        itemText.classList.add('-active');
      } else {
        elementHasClass(itemText, '-active') && itemText.classList.remove('-active');
      }
      if (itemText && itemText.innerText === 'All') {
        if (el.target.checked) {
          allCheckBox.forEach((chkbox) => {
            chkbox.checked = true;
            chkbox.parentElement.parentElement.lastElementChild.classList.add('-active');
          })
        } else {
          allCheckBox.forEach((chkbox) => {
            chkbox.checked = false;
            chkbox.parentElement.parentElement.lastElementChild.classList.remove('-active');
          })
        }
      }

      // handle check count
      const checkBoxCount = boxContainer && boxContainer.querySelectorAll(`input[name=${el.target.name}]:checked`).length;
      if (checkBoxCount > 0) {
        textContainer && textContainer.classList.remove('-hidecount');
        countBox && (countBox.innerHTML = checkBoxCount);
      } else {
        textContainer.classList.add('-hidecount');
        countBox && (countBox.innerHTML = '');
      }


      //handle reset
      const resetWrap = parentEle && parentEle.querySelector('.c-sp-filters__reset-wrap');
      const checkBoxList = parentEle && [...parentEle.querySelectorAll('.c-sp-filters__catg-checkbox')];
      const isChecked = checkBoxList && checkBoxList.some(check => (check.checked === true));
      isChecked ? (!elementHasClass(resetWrap, '-active') && addClassToElement(resetWrap, '-active')) :
        (elementHasClass(resetWrap, '-active') && removeClassFromElement(resetWrap, '-active'));
      let filterTextUpdate = parentEle && parentEle.querySelector('.c-sp-filters__text');
      // const allEle = checkBoxList.parentElement.nextElementSibling.innerText;
      const checkBoxListChecked = checkBoxList && [...checkBoxList.filter(item => (item.checked === true && item.parentElement.nextElementSibling.innerText !== 'All'))];
      filterTextUpdate.innerText = `Filter (${checkBoxListChecked.length})`;
    })
  })


  //  handle sort by and reset
  const selectedSort = getHtmlElmentsByClass("c-sp-filters__item");
  selectedSort.map((element) => {
    addEventListener(element, "click", () => {
      if (!element.classList.contains("-reset")) {
        removeClassFromElements("c-sp-filters__item", '-active');
      }
      if (element.classList.contains("-active")) {
        removeClassFromElement(element, '-active');
      } else {
        addClassToElement(element, '-active');
      }

      elementHasClass(element, '-alignitem') && sortTextChange(element);
    });
  });


  //Handle sort text change

  const sortTextChange = (ele) => {
    if (ele && ele.innerText && ele.innerText.includes('A - Z')) {
      ele.innerHTML = `Alphabetically Z - A <span class="c-sp-filters__sorticon"><i class="icon-sort-alpha"></i></span>`;
      addClassToElement(ele, '-rotate')
    } else {
      ele.innerHTML = 'Alphabetically A - Z <span class="c-sp-filters__sorticon"><i class="icon-sort-alpha"></i></span>';
      removeClassFromElement(ele, '-rotate');
    }
  }
  // Handle filter on scroll
  const filterHeader = getHtmlElmentsByClass('c-sp-filters__header');

  filterHeader.map((element) => {
    let currStickyPos = element.offsetTop;
    const filterCatgs = element.parentElement.lastElementChild.firstElementChild;
    const headerEle = document.querySelector('.c-header');
    let offsetEleHeight = headerEle.offsetHeight + 250;
    window.addEventListener("scroll", () => {
      let hasFilter = true;
      // handle workspace and template sub tab start
      let activebar = null;
      let activetab = null;
      let filterTab = null;
      const allbars = document.querySelectorAll('.c-workspace__content-bar');
      const allTabs = document.querySelectorAll('.c-workspace__content-tab');
      if(allbars.length > 0) {
        hasFilter = false;
        for (let index = 0; index < allbars.length; index++) {
          const element = allbars[index];
          if(element.classList.contains('-active')){
            activebar = element;
          }
        }
        for (let index = 0; index < allTabs.length; index++) {
          const element = allTabs[index];
          if(element.classList.contains('-is-open')){
            activetab = element;
            filterTab = activetab.querySelector('.c-sp-filters__wrap');
          }
        }
        if(filterTab && activebar.getAttribute('data-tab-trigger') == activetab.getAttribute('data-tab-content')){
          hasFilter = true;
        }
      }

      function getCurrentScroll() {
        return window.pageYOffset;
      }
    
      // handle workspace and template sub tab end
      leftMenuPosition();
      const scrollValue = getCurrentScroll();
      if(hasFilter){
        if(window.innerHeight > 900){
          offsetEleHeight -= 200;
        }
        if (scrollValue > offsetEleHeight) {
          element.classList.add('-fixedh');
          filterCatgs.classList.add('-fixedc');
          document.body.classList.add('-filterFix'); // added to hide the filter outside content
        }
        else {
          removeClassFromElement(element, '-fixedh');
          removeClassFromElement(filterCatgs, '-fixedc');
          document.body.classList.remove('-filterFix'); // added to hide the filter outside content
        }
      }
    });
  });

  function leftMenuPosition() {
    const footer = document.querySelector('.c-footer');
    const footerParent = footer && footer.closest(".site-wrap");
    const floatingLayer = document.querySelector('.c-sp-filters__catogories');
    if (footerParent && floatingLayer) {
      const windowBottomScroll = window.pageYOffset + window.innerHeight;
      const pageBounding = footerParent.getBoundingClientRect();
      //for footer
      const header = document.getElementById('header-container');

      if (header && ((windowBottomScroll) >= (pageBounding.height - footer.offsetHeight))) {
        floatingLayer.classList.add("-leftposition");
      } else {
        floatingLayer.classList.remove("-leftposition");
      }
    }
  }
};



