import { addClassToElement, addEventListener, elementHasClass, removeClassFromElement } from "./ui-scripts";

const isVisible = (el) => {
const rect = el.getBoundingClientRect();
    return (
        rect.left >= 0 &&
        rect.right <= (window.innerWidth || document.documentElement.clientWidth)
    );
}

export const developerHub = () => {
    const developerHubTileList = [...document.querySelectorAll('.c-developer-hub__tile-doc')];
    developerHubTileList && developerHubTileList.length > 0 && developerHubTileList.forEach(devItem => {
        addEventListener(devItem, "mouseenter", () => {
            const parentEle = devItem.closest('.c-developer-hub__tile')
            const tWrap = parentEle.querySelector('.c-developer-hub__twrap');
            tWrap && !elementHasClass(tWrap, '-active') && addClassToElement(tWrap, '-active');
            const isEleVisi = isVisible(tWrap);
            if(!isEleVisi) {
                tWrap && elementHasClass(tWrap, '-active') && removeClassFromElement(tWrap, '-active');
                tWrap && !elementHasClass(tWrap, '-ractive') && addClassToElement(tWrap, '-ractive');
            } else {
                tWrap && !elementHasClass(tWrap, '-active') && addClassToElement(tWrap, '-active');
            }
         });
        addEventListener(devItem, "mouseleave", () => {
            const parentEle = devItem.closest('.c-developer-hub__tile')
            const tWrap = parentEle.querySelector('.c-developer-hub__twrap');
            tWrap && elementHasClass(tWrap, '-active') && removeClassFromElement(tWrap, '-active');
            tWrap && elementHasClass(tWrap, '-ractive') && removeClassFromElement(tWrap, '-ractive');
        });
    });
}


