import {
    getHtmlElmentsByClass,
    addEventListener,
    removeClassFromElements
} from './ui-scripts';

export const newRequest = () => {
    const action = getHtmlElmentsByClass("-info");
    action.map((item, index) => {
        addEventListener(item, "click", () => {
            const popup = item.nextElementSibling;
            popup.classList.toggle("-show");
        })
    });


    // kpi details show more
    const dcskpiTemp = getHtmlElmentsByClass('c-sp-kpidetails__showmore');
 dcskpiTemp.map(template => {
  addEventListener(template, 'click', (el)=>{
    const parentContent = template.closest('.c-sp-kpidetails__datawrap');
    const toggle = !!parentContent.lastElementChild.classList.contains('-active');
    removeClassFromElements('c-sp-kpidetails__datatable', '-active');
    dcskpiTemp.map(item => {
      item.innerHTML = "Show more";
    })
    
    if(!toggle && !parentContent.lastElementChild.classList.contains('-active') && (el.target.innerHTML === "Show more")){
      parentContent.lastElementChild.classList.add('-active');
      template.innerHTML = "Show less";
    }
  })
})
}




