/**
 *
 */
export class SolutionCarousel {
  /**
   *
   * @param {*} id
   * @param {*} options
   */
  constructor(id, options = {}) {
    this.htmlElement = document.getElementById(id);
    this.itemsMarginSpace = 0;
    this.options = this.validateAndUpdateOptions(options);
    this.width = 0;
    this.itemWidth = 0;
    this.mainContainer = null;
    this.mainWrapper = null;
    this.wrapperElement = null;
    this.carouselItems = null;
    this.totalItemCount = 0;
    this.currentSlide = 1;
    this.leftArrow = null;
    this.rightArrow = null;
    this.arrowWrapper = null;
    this.dotsContainer = null;
    this.currentPage = 0;
    this.totalPages = undefined;
    if (this.htmlElement) {
      this.init();
      this.renderCarousel();
      this.addEventListener(window, "resize", () => {
        this.renderCarousel();
      });
    }
  }

  init() {
    this.mainContainer = document.createElement("div");
    this.wrapperElement = document.createElement("div");
    this.mainWrapper = document.createElement("div");
    this.leftArrow = document.createElement("div");
    this.rightArrow = document.createElement("div");
    this.arrowWrapper = document.createElement("div");
    this.dotsContainer = document.createElement("div");
    this.addEventListener(this.rightArrow, "click", () => {
      if (!this.rightArrow.classList.contains("disabled")) {
        this.gotoNextSlide();
        this.appendDisableClassInArrows();
        this.dotsStatus();
      }
    });

    this.addEventListener(this.leftArrow, "click", () => {
      if (!this.leftArrow.classList.contains("disabled")) {
        this.gotoPreviousSlide();
        this.appendDisableClassInArrows();
        this.dotsStatus();
      }
    });
  }

  setDataByWindowSize() {
    const windowWidth = window.innerWidth;
    const responsiveArray = this.options.responsive;
    if (responsiveArray && responsiveArray.length > 0) {
      const value = responsiveArray.find((val) => val.maxWidth <= windowWidth);
      if (value) {
        this.options.items = value.items;
      } else {
        this.options.items = 1;
      }
    }
  }

  initCalculation() {
    this.width = this.htmlElement.offsetWidth;
    this.itemWidth =
      (this.width - 100) / this.options.items - (this.itemsMarginSpace / 2);
    this.currentSlide = 1;
  }

  getHtmlCollection(collection) {
    let HtmlArray = [];
    for (let i = 0; i < collection.length; i++) {
      HtmlArray.push(collection[i]);
    }
    return HtmlArray;
  }

  addEventListener(element, eventName, eventHandler) {
    if (element && element.addEventListener) {
      element.addEventListener(eventName, eventHandler, false);
    } else if (element && element.attachEvent) {
      element.attachEvent("on" + eventName, eventHandler);
    }
  }

  addClassToElement(element, className) {
    element.classList.add(className);
  }

  removeClassFromElement(element, className) {
    element.classList.remove(className);
  }

  validateAndUpdateOptions(options) {
    const {
      items = 1,
      firstItemFixed = false,
      arrows = false,
      dots = false,
      margin = 0,
    } = options;
    this.itemsMarginSpace = margin;
    return Object.assign(options, {
      items,
      firstItemFixed,
      arrows,
      dots,
    });
  }

  setCarouselItems() {
    this.carouselItems = this.getHtmlCollection(
      this.htmlElement.querySelectorAll(".carousel-item")
    );
  }

  getScrollableIndex(slideValue) {
    slideValue = slideValue - 1;
    let slideUpto = this.totalItemCount - this.options.items;
    if (slideValue < 1) {
      slideValue = 0;
    } else if (slideValue > slideUpto) {
      slideValue = slideUpto;
    }
    return slideValue;
  }

  getScrollTransition(slideValue) {
    let slideTo = this.getScrollableIndex(slideValue);
    this.currentSlide = slideTo + 1;
    return this.getItemsWidth() * slideTo;
  }

  goToSlide(slideValue) {
    let scrollTransition = this.getScrollTransition(slideValue);
    this.currentPage = Math.ceil((slideValue - 1) / this.options.items);
    this.appendDisableClassInArrows();
    this.wrapperElement.style.transform = `translate3d(-${scrollTransition}px, 0px, 0px)`;
  }

  getItemsWidth() {
    return this.itemWidth + this.itemsMarginSpace / 2;
  }

  appendWrapperOnElements(elementsArray) {
    this.addClassToElement(this.mainContainer, "main-x-container");
    this.addClassToElement(this.wrapperElement, "carousel-wrapper");
    this.wrapperElement.style.width = `${
      this.getItemsWidth() * this.totalItemCount
    }px`;
    this.wrapperElement.style.transition = `all 0.5s ease 0s`;
    elementsArray.map((item) => {
      this.wrapperElement.append(item);
    });

    this.mainWrapper.append(this.wrapperElement);
    this.mainContainer.append(this.mainWrapper);
    this.htmlElement.append(this.mainContainer);
  }

  appendWrapper() {
    this.addClassToElement(this.mainWrapper, "main-x-wrapper");
    this.carouselItems.map((item) => {
      item.style.width = `${this.itemWidth}px`;
      item.style.marginRight = this.itemsMarginSpace / 4 + "px";
      item.style.marginLeft = this.itemsMarginSpace / 4 + "px";
      // item.style.float = `left`;
      item.style.display = 'inline-flex';
      item.style.justifyContent = 'center';
    });

    if (!this.options.firstItemFixed) {
      this.totalItemCount = this.carouselItems.length;
      this.appendWrapperOnElements(this.carouselItems);
    } else {
      this.totalItemCount = this.carouselItems.length - 1;
      this.mainWrapper.append(this.carouselItems[0]);
      this.addClassToElement(this.carouselItems[0], "x-first-element");
      this.appendWrapperOnElements(
        this.carouselItems.slice(1, this.carouselItems.length)
      );
      this.options.items = this.options.items - 1;
    }

    if (this.totalItemCount > this.options.items) {
      this.options.arrows = true;
    } else {
      this.options.arrows = false;
    }
  }

  gotoPreviousSlide() {
    this.goToSlide(this.currentSlide - 1);
  }

  gotoNextSlide() {
    this.goToSlide(this.currentSlide + 1);
  }

  totalSlides() {
    return this.totalItemCount - this.options.items;
  }

  isLastSlide() {
    let endSlide = this.totalSlides();
    return endSlide + 1 === this.currentSlide;
  }

  isFirstSlide() {
    return this.currentSlide === 1;
  }

  appendDisableClassInArrows() {
    if (this.isFirstSlide()) {
      this.addClassToElement(this.leftArrow, "disabled");
    } else {
      this.removeClassFromElement(this.leftArrow, "disabled");
    }

    if (this.isLastSlide()) {
      this.addClassToElement(this.rightArrow, "disabled");
    } else {
      this.removeClassFromElement(this.rightArrow, "disabled");
    }
  }

  appendArrows() {
    this.addClassToElement(this.leftArrow, "x-arrow");
    this.addClassToElement(this.leftArrow, "x-left-arrow");
    this.leftArrow.innerHTML = this.options.leftArrow ?
      this.options.leftArrow :
      "left";

    this.addClassToElement(this.rightArrow, "x-arrow");
    this.addClassToElement(this.rightArrow, "x-right-arrow");
    this.rightArrow.innerHTML = this.options.leftArrow ?
      this.options.rightArrow :
      "right";

    this.addClassToElement(this.arrowWrapper, "x-arrows");

    this.arrowWrapper.append(this.leftArrow);
    this.arrowWrapper.append(this.rightArrow);
    this.htmlElement.append(this.arrowWrapper);
    if (this.options.arrows) {
      this.removeClassFromElement(this.arrowWrapper, "-hide");
    } else {
      this.addClassToElement(this.arrowWrapper, "-hide");
    }
  }

  goToPage(pageNo) {
        // this.currentSlide = pageNo * this.options.items;
    // this.currentPage = pageNo;
    // this.goToSlide(pageNo * this.options.items);

    // dots fix
    this.currentSlide = pageNo * this.options.items + 1;
    this.currentPage = pageNo;
    this.goToSlide(this.currentSlide);
  }

  appendDots() {
    if (this.options.dots) {
      this.addClassToElement(this.dotsContainer, "x-dots-container");
      this.dotsContainer.innerHTML = null;
      const dotsWrapper = document.createElement("div");
      this.addClassToElement(dotsWrapper, "x-dots-wrapper");

      this.totalPages = Math.ceil(this.totalItemCount / this.options.items);

      if (this.totalPages > 1) {
      [...Array(this.totalPages)].map((_, index) => {
        const dotElement = document.createElement("span");
        this.addClassToElement(dotElement, "x-dot-item");
        dotsWrapper.append(dotElement);
        this.addEventListener(dotElement, "click", () => {
          this.goToPage(index);
          this.dotsStatus();
        });
      });
      this.dotsContainer.append(dotsWrapper);
      this.mainContainer.append(this.dotsContainer);
      }
    }
  }

  dotsStatus() {
    if (this.options.dots && this.dotsContainer.firstElementChild) {
      const dotItems = this.getHtmlCollection(
        this.dotsContainer.firstElementChild.children
      );
      dotItems.map((elem, index) => {
        if (index === this.currentPage) {
          this.removeClassFromElement(elem, "-disabled");
        } else {
          this.addClassToElement(elem, "-disabled");
        }
      });
    }
  }

  renderCarousel() {
    this.setDataByWindowSize();
    this.initCalculation();
    this.setCarouselItems();
    this.appendWrapper();
    this.appendArrows();
    this.appendDisableClassInArrows();
    this.appendDots();
    this.goToSlide(1);
    this.dotsStatus();
  }
}

export const xCarousel = () => {
  window.SolutionCarousel = SolutionCarousel;
};



