import {
  addClassToElement
} from "./ui-scripts";
//To add right icon after title
const solutionTileTxt = (id) => {
  let truncLineParent = [],
    truncLine = [];
  // To select all solution catalogue section
  const parentId = id ? id : "solution-tile";
  truncLineParent = document.querySelectorAll("#" + parentId);
  if (truncLineParent.length != 0) {
    for (let elm = 0; truncLineParent.length > elm; elm++) {
      let tiles = [];
      tiles = truncLineParent[elm].querySelectorAll(".c-uc-solution__tile-tcontent");
      if (tiles.length > 0) {
        // to reset the title text on reload of script start
        tiles.forEach(tile => {
          const txtParent = tile.closest('.c-uc-solution__tile-titletxt')
          const leafElm = txtParent.querySelector('.c-uc-solution__tile-swrap');
          if (leafElm) {
            leafElm.removeAttribute('style');
            leafElm.classList.remove('-set-right');
          }
          if (tile.classList.contains('c-uc-solution__trunkLine-one')) {
            tile.classList.remove('c-uc-solution__trunkLine-one');
          }
          if (tile.classList.contains('-word-break')) {
            tile.classList.remove('-word-break');
          }

          tile.innerText = tile.innerText.replace(/\n/g, " ");
        })
        //reste end

        truncLine = [...truncLine, ...tiles];
      }
    }
  }
  // selection end
  (truncLine.length !== 0) && truncLine.forEach(element => {
    const elmParent = element.closest('.c-uc-solution__tile-title ');
    let leaficon = null,
      innovicon = null;
    if (elmParent) {
      leaficon = elmParent.querySelector('.c-uc-solution__leaf');
      innovicon = elmParent.querySelector('.c-uc-solution__flag');
    }

    //process only if icon present
    if (element.offsetParent && (leaficon || innovicon)) {
      const textContainer = element.closest('.c-uc-solution__tile-title');
      let offsetWidthGap = element.offsetParent.offsetWidth - element.offsetWidth;

      //set text width if icon present
      if (!textContainer.classList.contains('-right-icon')) {
        textContainer.classList.add('-right-icon');
      }

      // to handle icon if more gap between element and element parent
      if (element.offsetHeight >= 44 && offsetWidthGap > 60) {

        // to get first line and second line text length
        let contPWidth = element.offsetParent.offsetWidth / 11
        let lineOneText = element.textContent.substring(0, contPWidth);
        let lastIndex = 0;
        lineOneText.split('').forEach((letter, i) => {
          if (letter == " ") {
            lastIndex = i;
          }
        });
        let secondLineText = element.textContent.substring(parseInt(lastIndex) + 1);
        // end

        if ((contPWidth < (element.innerText.length / 2)) || (secondLineText.length >= (contPWidth - 5))) {
          element.parentElement.lastElementChild.style.right = (offsetWidthGap - 40) + "px";
        }
      }

      // To handle right icon alignment with text in second line
      if (element.offsetHeight > 25 && offsetWidthGap < 62) {
        let lastIndex = 0;
        let containerWidth = Math.floor(element.parentElement.offsetWidth / 11);
        let lineOneText = element.textContent.substring(0, containerWidth);

        //check the title text have long word before any space
        lineOneText.split('').forEach((letter, i) => {
          if (letter == " ") {
            lastIndex = i;
          }
        });

        //add span element if text is more than two lines
        if (lastIndex) {
          let cloneText = element.textContent;
          element.textContent = cloneText.substring(0, lastIndex);
          let secondLineText = cloneText.substring(parseInt(lastIndex) + 1);

          if ((containerWidth <= secondLineText.length) && lastIndex) {
            let node = document.createElement("span");
            node.className = "c-uc-solution__trunkLine-one";
            let secondLine = document.createTextNode(secondLineText);
            node.appendChild(secondLine);
            element.appendChild(node);
            element.parentElement.lastElementChild.removeAttribute('style');
            addClassToElement(element.parentElement.lastElementChild, '-set-right');
          } else {
            element.textContent = cloneText;
          }
        } else if (lastIndex === 0) {
          addClassToElement(element, '-word-break');
          element.parentElement.lastElementChild.removeAttribute('style');
          addClassToElement(element.parentElement.lastElementChild, '-set-right');
        }
      }
    }
  });
}

export const solTileTxt = () => {
  window.onload = () => {
    solutionTileTxt()
  } // Call the function after complete load of page for tile text consistency in tabs
  window.solutionTileTxt = solutionTileTxt;
  window.addEventListener("resize", () => {
    solutionTileTxt();
  });
};


