export const tooltipHover = () => {
  var targets = document.querySelectorAll('[rel="tooltip"]');
  for (var i = 0; i < targets.length; i++) {
    targets[i].addEventListener(
      "mouseenter",
      function () {
        addTooltip(this, document.querySelector(".c-tooltip"));
      },
      false
    );
    targets[i].addEventListener(
      "mouseleave",
      function () {
        removeTooltip(this, document.querySelector(".c-tooltip"));
      },
      false
    );
    targets[i].addEventListener(
      "click",
      function () {
        var tooltip = document.querySelector(".c-tooltip");
        var target = event.target.closest('[rel="tooltip"]');
        if (tooltip === null) {
          addTooltip(target, tooltip);
        } else {
          removeTooltip(target, tooltip);
        }
      },
      false
    );
  }
  function addTooltip(target, tooltip) {
    var title = target.getAttribute("title");
    const name = target.getAttribute("name");
    const getBlock = document.getElementById("tooltip-right");
    const newNode = document.importNode(getBlock, true);
    if ((title === null || title === "" || tooltip !== null) && name === null) {
      return false;
    }
    if (name)
    {
      // To show tooltip at right side
      target.insertAdjacentElement("beforeend", newNode);
      target.childNodes[0].classList.add("icon-tooltip");
      target.childNodes[0].classList.remove("icon-info");
    }
    else
    {
      tooltip = document.createElement("div");
      target.removeAttribute("title");
      tooltip.setAttribute("class", "c-tooltip");
      tooltip.innerHTML = title;
      target.insertAdjacentElement("beforeend", tooltip);
      tooltip.style.right = "-30px";
      if(target.firstElementChild.classList.contains("-right")){
        tooltip.classList.add("c-tooltip__lefttip");
        tooltip.style.width = tooltip.offsetWidth + 100 + "px";
        target.firstElementChild.classList.add("icon-tooltip");
        tooltip.style.left = (tooltip.offsetWidth / 6 - 30) + "px";
        tooltip.style.top = -(target.offsetTop + target.offsetHeight - 15) + "px";
      }
      else {
        tooltip.style.width = tooltip.offsetWidth + "px";
        tooltip.style.left = -(tooltip.offsetWidth / 4 - 10) + "px";
        tooltip.style.top = target.offsetTop + target.offsetHeight + 15 + "px";
      }
    }
  }
  function removeTooltip(target, tooltip) {
    if (tooltip !== null) {
      target.setAttribute("title", tooltip.innerHTML);
      if(target.firstElementChild.classList.contains("-right")){
        target.firstElementChild.classList.remove("icon-tooltip");
      }
      tooltip.remove();
    }
    // To remove right tooltip
    while (target.childNodes.length >1 && target.hasChildNodes()){
      target.removeChild(target.lastChild);
    }
    if(target.name !== "" && target.childNodes[0].classList.contains("icon-tooltip")){
      target.childNodes[0].classList.add("icon-info");
      target.childNodes[0].classList.remove("icon-tooltip");
    }
  }
};



