export const updateAlert = () => {
  /*To close the update popup when trigger the close icon */
  const closeBtns = [].slice.call(
    document.querySelectorAll(".c-update-popup__close-button")
  );
  closeBtns.forEach(function (btn) {
    btn.onclick = function () {
      let modal = btn.closest(".c-update-popup");
      modal.style.display = "none";
    };
  });
}


