 import {
  addEventListener,
  getHtmlElmentsByClass,
  getElementById
} from "./ui-scripts";

export const catagoriesShowMore = () => {
  // show more expand start
  var allShowmoreExp = getHtmlElmentsByClass("c-sp-filters__exp-more");
  allShowmoreExp.map(btn => {
    btn.addEventListener('click', ()=>{
      const btnwrapper = btn.closest(".c-sp-filters__catg-listwrap")
      const btnParent = btnwrapper.querySelector('.c-sp-filters__catg-list');
      const moretext = btn.querySelector('.-moretext');
      if(btnParent && moretext && btnParent.classList.contains("-show")){
        btnParent.classList.remove("-show");
        moretext.innerText = "more";
      } else {
        btnParent.classList.add("-show");
        moretext.innerText = "less";
      }
    })
  })
  // show more expand end
 // Insight Triggers
    var showmoreBtn = document.querySelectorAll(".c-sp-filters__more");
    var showmorecloseBtn = document.querySelectorAll(".c-catagories-more__close-button");
    showmoreBtn.forEach(item => {
      item.addEventListener("click", () => {
        showmore(item);
        footerHandler(true);
    });
    });

    showmorecloseBtn.forEach(item => {
      // Edit Trigger;
      item.addEventListener("click", () => {
        closeshowmore(item);
        footerHandler(false);
    });
    });

     function closeshowmore(item) {
        const parentWrapper = item.closest(".c-sp-filters__catgwrap");
        const showmoreWrapper = parentWrapper.lastElementChild;
        const scrollParent = item.closest('.c-sp-filters__catg-wrap');
        const contentWrapper = parentWrapper.querySelector('.c-catagories-more__select-wrap');

        if(showmoreWrapper && showmoreWrapper.classList.contains('c-catagories-more')) {
          if(showmoreWrapper.classList.contains('-active')) {
            showmoreWrapper.classList.remove('-active');
            // scrollParent.style.overflow = "auto";
            contentWrapper.removeAttribute('style');
            scrollParent.removeAttribute('style');
        }
        document.body.classList.remove('-filter-select');
       }
     }

    function showmore(item) {
      const parentWrapper = item.closest(".c-sp-filters__catgwrap");
      var showmoreWrapper = parentWrapper.lastElementChild;
      var scrollParent = item.closest('.c-sp-filters__catg-wrap');
      const contentWrapper = parentWrapper.querySelector('.c-catagories-more__select-wrap');
      const bottom = viewAtBottom();
      const windowResolution = window.screen.availWidth;

      if(showmoreWrapper && showmoreWrapper.classList.contains('c-catagories-more')) {
        if(!showmoreWrapper.classList.contains('-active')) {
          showmoreWrapper.classList.add('-active');
          scrollParent.style.overflow = "visible";
          scrollParent.style.position = 'relative';
          const itemBounding = parentWrapper.getBoundingClientRect();
          const filterHead = document.querySelector('.c-sp-filters__wrap').getBoundingClientRect();
          if((bottom.atbottom && windowResolution <= 1280) || windowResolution <= 1280) {
            contentWrapper.style.height = '130px';
            scrollParent.style.top = (filterHead.top > 0) ? `-${itemBounding.y - filterHead.top - (((itemBounding.y/7)> 110)?(itemBounding.y/7):110)}px` : `-${itemBounding.y - (((itemBounding.y/7)> 120)?(itemBounding.y/7):120)}px`;
          }else {
            if(bottom.atbottom) {
              contentWrapper.style.height = '170px';
            }
            scrollParent.style.top = (filterHead.top > 0) ? `-${itemBounding.y - filterHead.top - (((itemBounding.y/7)> 130)?(itemBounding.y/7):130)}px` : `-${itemBounding.y - (((itemBounding.y/7)> 150)?(itemBounding.y/7):150)}px`;
          }
        }
        document.body.classList.add('-filter-select');
      }
    }

    function viewAtBottom() {
      const footer = document.querySelector('.c-sp-footer');
      const footerParent = footer && footer.closest(".site-wrap");
      const floatingLayer = document.querySelector('.c-sp-filters__catogories');
      if (footerParent && floatingLayer) {
        const windowBottomScroll = window.pageYOffset + window.innerHeight;
        const pageBounding = footerParent.getBoundingClientRect();
        //for footer

        if ((windowBottomScroll) >= (pageBounding.height - footer.offsetHeight)) {
          return {atbottom: true, winbottomscroll: windowBottomScroll};
        } else {
          return {atbottom: false, winbottomscroll: windowBottomScroll};
        }
      }
    }
}

function footerHandler(showLayer = true) {
  const windowBottomScroll = window.pageYOffset + window.innerHeight;
  const footer = document.querySelector(".c-sp-footer");
  const filterContainer = document.querySelectorAll(
    ".c-sp-filters__catogories"
  );
  const filterList = document.querySelectorAll(".c-sp-filters__catgwrap");
  const parentContainer = document.querySelector(".site-wrap");
  const windowWidth = window.innerWidth;
  const allCatg = document.querySelectorAll(".c-catagories-more");
  const allCatgName = document.querySelectorAll(
    ".c-sp-filters__catgtitle"
  );
  let activeCatgParent, activeCatgName;

  if (footer) {
    allCatg.forEach((catg) => {
      if (catg.classList.contains("-active")) {
        activeCatgParent = catg.closest(".c-sp-filters__catgwrap");
        activeCatgParent &&
          (activeCatgName = activeCatgParent.querySelector(
            ".c-sp-filters__catgtitle"
          ));
      }
    });

    if (showLayer && windowWidth <= 1650 && document.body.classList.contains("-filter-select") && footer && windowBottomScroll >= parentContainer.offsetHeight - 140) {
      footer.style.zIndex = "-1";
      filterContainer.forEach((catg) => {
        catg.classList.add("-ucfooterfix");
      });
      if (activeCatgParent) {
        allCatgName.forEach((item) => {
          item.style.display = "none";
        });
        activeCatgName && activeCatgName.removeAttribute("style");
      }
      if (filterList && filterList.length > 0) {
        filterList.forEach((item) => {
          const filterSearch = item.querySelector(".c-sp-filters__search");
          const filterListItem = item.querySelector(
            ".c-sp-filters__catg-list"
          );
          filterSearch && (filterSearch.style.opacity = "0");
          filterListItem && (filterListItem.style.opacity = "0");
        });
      }
    } else {
      const filterLayer = document.querySelectorAll(
        ".c-catagories-more__select-wrap"
      );
      if (filterLayer.length > 0 && window.innerHeight > 600) {
        filterLayer.forEach((layer) => {
          layer.style.height = "140px";
        });
      }
      footer && footer.removeAttribute("style");
      filterContainer.forEach(catg => {
        catg.classList.remove('-ucfooterfix');
      })
      allCatgName.forEach(item => {
        item.removeAttribute('style');
      })
      if(filterList && filterList.length > 0){
        filterList.forEach(item => {
          const filterSearch = item.querySelector('.c-sp-filters__search');
          const filterListItem = item.querySelector('.c-sp-filters__catg-list');
          filterSearch && (filterSearch.removeAttribute('style'));
          filterListItem && (filterListItem.removeAttribute('style'));
        })
      }
    }
  }
}

window.addEventListener("scroll", function (e) {
  footerHandler();
});


