//binary search for the range of "visible" tags
function bisectLeft(arr, val) {
  var lower = 0,
    upper = arr.length - 1;
  if (val >= arr[upper])      //edge condition #1
    return upper;           
  if (val < arr[0])           //edge condition #2
    return -1;              
  while (lower <= upper) {
    var mid = lower + Math.floor((upper - lower) / 2);
    if ((arr[mid] <= val) && (arr[mid + 1] > val))
      return mid;
    if (val <= arr[mid])
      upper = mid;
    else lower = mid;
  }
}
//search tags
const search_tags = () => {
  //precomputation
  const tags_container = document.querySelectorAll(".c-sp-search-results__tags");
  tags_container.forEach((tag_cont) => {
    var total_child_node_width = 0;
    var suffix_width = [];
    const child_nodes = tag_cont.querySelectorAll(".c-tags-comp__tag");
    child_nodes.forEach((cnode) => {
      total_child_node_width += cnode.offsetWidth + 10 + 10;
      suffix_width.push(total_child_node_width);
    });

    //calculate initial position
    var curr_pos = 0;

    function more_btn_init() {
      var rm = document.createElement('div');
      rm.innerHTML = "More...";
      rm.className = "c-sp-search-results__tags-morebtn";
      curr_pos = bisectLeft(suffix_width, tag_cont.offsetWidth - 50);
      tag_cont.insertBefore(rm, child_nodes[child_nodes.length]);
      if(curr_pos == -1)
      {
        for (var i = 0; i < suffix_width.length; i++)
            child_nodes[i].classList.add("-hidden");
      } else {
          for (var i = 0; i < suffix_width.length; i++) {
            if (i > curr_pos)
              child_nodes[i].classList.add("-hidden");
            else child_nodes[i].classList.remove("-hidden");
        }
      }
    }

    //more_btn_click fn
    function btn_click() {
      var more_btn = tag_cont.querySelector(".c-sp-search-results__tags-morebtn");
      if (more_btn.classList.contains("-expanded")) {
        for (var i = curr_pos + 1; i < suffix_width.length; i++)
          child_nodes[i].classList.add("-hidden");
        more_btn.innerHTML = "More..."
        more_btn.classList.remove("-expanded");
      } else {
        for (var i = curr_pos + 1; i < suffix_width.length; i++)
          child_nodes[i].classList.remove("-hidden");
        more_btn.innerHTML = "Less"
        more_btn.classList.add("-expanded");
      }
    }
    if (tag_cont.offsetWidth < total_child_node_width) {
      more_btn_init();
      tag_cont.querySelector(".c-sp-search-results__tags-morebtn").addEventListener("click", btn_click);
    }

    //resize event
    window.addEventListener("resize", () => {
      if (tag_cont.offsetWidth < total_child_node_width) {
        if (tag_cont.querySelector(".c-sp-search-results__tags-morebtn"))
          tag_cont.querySelector(".c-sp-search-results__tags-morebtn").remove();
        more_btn_init();
        tag_cont.querySelector(".c-sp-search-results__tags-morebtn").addEventListener("click", btn_click);
      } else {
        if (tag_cont.querySelector(".c-sp-search-results__tags-morebtn"))
          tag_cont.querySelector(".c-sp-search-results__tags-morebtn").remove();
        for (var i = 0; i < suffix_width.length; i++)
          child_nodes[i].classList.remove("-hidden");
      }
    });
  });
}

//advanced filter 
const adv_filter_dropdown = () => {
  var adv_filters = document.querySelectorAll(".c-sp-search-results__advfilter");
  adv_filters.forEach((filter) => {
    filter.addEventListener("click", () => {
      if (filter.classList.contains("-filter-open")) {
        filter.classList.remove("-filter-open");
        filter.parentNode.parentNode.querySelector(".c-sp-search-results__advfilter-cont").classList.remove("-adv-filter-open");
      } else {
        filter.classList.add("-filter-open");
        filter.parentNode.parentNode.querySelector(".c-sp-search-results__advfilter-cont").classList.add("-adv-filter-open");
      }
    });
  });
}

//tags click event
const search_tags_activity = () => {
  var results_container = document.querySelectorAll(".c-sp-search-results");
  results_container.forEach((cont) => {
    var all_tags = cont.querySelectorAll(".c-tags-comp__tag");
    all_tags.forEach((filter_tag) => {
      filter_tag.addEventListener("click", () => {
        filter_tag.classList.toggle("-active");
      });
    });
  });
}

//search-bar activity
const searchbox_activity = () => {
  var results_container = document.querySelectorAll(".c-sp-search-results");
  results_container.forEach((cont) => {
    var input_boxes = cont.querySelectorAll(".c-sp-search-results__input-box");
    input_boxes.forEach(input_box => {
      input_box.addEventListener("keyup", () => {
        if (!input_box.value) {
          input_box.parentNode.classList.remove("-search-active");
          return;
        }
        input_box.parentNode.classList.add("-search-active");
      });
    });

    //input-close btns
    var close_btns = cont.querySelectorAll(".c-sp-search-results__input-close");
    close_btns.forEach(close => {
      close.addEventListener("click", () => {
        close.parentNode.classList.remove("-search-active");
        close.parentNode.querySelector(".c-sp-search-results__input-box").value = "";
      });
    });
  });
}
export const search_results_fn = () => {
  search_tags();
  adv_filter_dropdown();
  search_tags_activity();
  searchbox_activity();
}



