import {
  addClassToElement,
  addEventListener,
  elementHasClass,
  removeClassFromElement,
} from "./ui-scripts";

import { modalCarouselList } from "./carouselInit";
/**
 * Sort Work Space Modal
 */

const sortWorkspaceModalTrigger = () => {
  const openSort = document.querySelector(".-sort-trigger .-open");
  const closeSort = document.querySelector(".-sort-trigger .-close");
  const overlay = document.querySelector(".c-workspace__overlay");
  const sort = document.querySelector(".c-workspace__sort");
  const body = document.querySelector("body");

  const openSortTrigger = () => {
    overlay.style.display = "block";
    sort.style.display = "block";
    // body.style.overflow = "hidden";
  };

  const closeSortTrigger = () => {
    overlay.style.display = "none";
    sort.style.display = "none";
    // body.style.overflow = "visible";
  };

  if (openSort && closeSort) {
    addEventListener(openSort, "click", openSortTrigger);
    addEventListener(closeSort, "click", closeSortTrigger);
    addEventListener(overlay, "click", closeSortTrigger);
  }
};

const menuToggle = () => {
  const menuBar = document.querySelector(".c-header__menu");
  const menuOpenBtn = document.querySelector(".c-header .-menu-open");
  const menuCloseBtn = document.querySelector(".c-header .-menu-close");
  const overlay = document.querySelector(".c-header__overlay");
  const body = document.querySelector("body");

  const openMenu = () => {
    overlay.style.display = "block";
    addClassToElement(menuBar, "-open");
    body.style.overflow = "hidden";
  };

  const closeMenu = () => {
    overlay.style.display = "none";
    removeClassFromElement(menuBar, "-open");
    body.style.overflow = "visible";
  };

  if (menuBar) {
    addEventListener(menuOpenBtn, "click", openMenu);
    addEventListener(menuCloseBtn, "click", closeMenu);
    addEventListener(overlay, "click", closeMenu);
  }
};

const userToggle = () => {
  const userIcon = document.querySelector(".c-header__user");
  const userLinks = document.querySelector(".c-header__user-links");
  const overlay = document.querySelector(".c-header__overlay");

  const showUserLinks = () => {
    overlay.style.display = "block";
    userLinks.style.display = "block";
    addClassToElement(userIcon, "-active");
  };

  const hideUserLinks = () => {
    overlay.style.display = "none";
    userLinks.style.display = "none";
    removeClassFromElement(userIcon, "-active");
  };

  if (userIcon) {
    addEventListener(userIcon, "click", () => {
      if (!elementHasClass(userIcon, "-active")) {
        showUserLinks();
      } else {
        hideUserLinks();
      }
    });
    addEventListener(overlay, "click", hideUserLinks);
  }
};

/* Modal popup code starts */
export const modalWindow = () => {
  sortWorkspaceModalTrigger();
  menuToggle();
  userToggle();

  if (!Element.prototype.matches) {
    Element.prototype.matches =
      Element.prototype.msMatchesSelector ||
      Element.prototype.webkitMatchesSelector;
  }

  if (!Element.prototype.closest) {
    Element.prototype.closest = function (s) {
      var el = this;

      do {
        if (el.matches(s)) return el;
        el = el.parentElement || el.parentNode;
      } while (el !== null && el.nodeType === 1);
      return null;
    };
  }

  // Modal Window
  const body = document.querySelector("body");
  const modalBtns = [].slice.call(document.querySelectorAll(".c-modal-btn"));
  modalBtns.forEach(function (btn) {
    btn.onclick = function () {
      let modal = btn.getAttribute("data-modal");
      var modalWindow = document.getElementById(modal);
      modalWindow && (modalWindow.style.display = "block");
      modalWindow && (body.style.overflow = "hidden");
      // for modals with carousel, render them
      var carousels = modalWindow && modalWindow.querySelectorAll(".industry-x-carousel");
      if(carousels && carousels.length)
      {
        carousels.forEach((car_element) => {
          var carousel_id = car_element.getAttribute("id");
          for(var i = 0; i < modalCarouselList.length; i++)
          {
            if(modalCarouselList[i].id == carousel_id)
              modalCarouselList[i].object.renderCarousel();
          }
        })
      }
    };
  });

  /*To close the modal popup when trigger the close icon */
  const closeBtns = [].slice.call(
    document.querySelectorAll(".c-modal__close-button")
  );
  closeBtns.forEach(function (btn) {
    btn.onclick = function () {
      let modal = btn.closest(".c-modal");
      modal.style.display = "none";
      body.style.overflow = "auto";
    };
  });

  /*To close the modal popup when trigger the window click */
  window.onclick = function (event) {
    if (event.target.className === "c-modal") {
      event.target.style.display = "none";
      body.style.overflow = "auto";
    }
  };
};



