import {
    addClassToElement,
    getHtmlElmentsByClass,
    addEventListener,
    elementHasClass,
    removeClassFromElement,
  } from "./ui-scripts";
  
  const showMoreDefinition = () => {

    const readMorebtn = getHtmlElmentsByClass("c-solution-overview__more");
    readMorebtn.map((item, index) => {
        addEventListener(item, "click", () => {
            const readmoreList = item.parentElement;
            if(!elementHasClass(readmoreList, "-active")) {
              addClassToElement(readmoreList, "-active");
              item.innerText = "Read Less";
            }else {
              removeClassFromElement(readmoreList, "-active");
              item.innerText = "Read More";
            }
            // readmoreList.classList.toggle("-active");
        })
    });
  };

  const showMoreTags = () => {
    const expand = getHtmlElmentsByClass("c-tags-comp__more");
    expand.map((showTag, index) => {
      addEventListener(showTag, "click", () => {
        const content = showTag.previousElementSibling;
        if(!elementHasClass(content, "-show")) {          
          addClassToElement(content, "-show");
          addClassToElement(showTag, "-highlight");
          showTag.innerText = "Less";
        }
        else {
          removeClassFromElement(content, "-show");
          removeClassFromElement(showTag, "-highlight");
          showTag.innerText = "More...";
        }


      });
    });
  };

  const showMoreChallenges = () => {

    const readMorebtn = getHtmlElmentsByClass("c-challenges-new-dcs__more");
    readMorebtn.map((item, index) => {
        addEventListener(item, "click", () => {
            const readmoreList = item.parentElement;
            if(!elementHasClass(readmoreList, "-active")) {
              addClassToElement(readmoreList, "-active");
              item.innerText = "Read Less";
            }else {
              removeClassFromElement(readmoreList, "-active");
              item.innerText = "Read More";
            }
            // readmoreList.classList.toggle("-active");
        })
    });
  };

  const showMoreUsecase = () => {

    const readMorebtn = getHtmlElmentsByClass("c-usecase__more");
    readMorebtn.map((item, index) => {
        addEventListener(item, "click", () => {
            const readmoreList = item.parentElement;
            if(!elementHasClass(readmoreList, "-active")) {
              addClassToElement(readmoreList, "-active");
              item.innerText = "Read Less";
            }else {
              removeClassFromElement(readmoreList, "-active");
              item.innerText = "Read More";
            }
            // readmoreList.classList.toggle("-active");
        })
    });
  };

  const showMoreAdd = () => {

    const readMorebtn1 = getHtmlElmentsByClass("c-usecase__read-more");
    readMorebtn1.map((item, index) => {
        addEventListener(item, "click", () => {
            const readmoreList = item.parentElement;
            if(!elementHasClass(readmoreList, "-active")) {
              addClassToElement(readmoreList, "-active");
              item.innerText = "Read Less";
            }else {
              removeClassFromElement(readmoreList, "-active");
              item.innerText = "Read More";
            }
            // readmoreList.classList.toggle("-active");
        })
    });
  };

  const showMoreCatg = () => {
    const showMoreCatg = getHtmlElmentsByClass("c-sp-filters__catgmore");  
    showMoreCatg.map((showMoreCatg, index) => {
      addEventListener(showMoreCatg, "click", () => {
        const contentText = showMoreCatg.previousElementSibling;
        if(!elementHasClass(contentText, "-show")) {          
          addClassToElement(contentText, "-show");
          showMoreCatg.innerText = "Show Less";
        }
        else {
          removeClassFromElement(contentText, "-show");
          showMoreCatg.innerText = "10 More";
        }
      });
    });
  };


  export const moreBtn = () => {
    showMoreDefinition();
    showMoreTags();
    showMoreChallenges();
    showMoreUsecase();
    showMoreAdd();
    showMoreCatg();
  };



