import {
  getHtmlElmentsByClass,
  addEventListener,
  addClassToElement,
  removeClassFromElement,
  removeClassFromElements,
  getQueryHtmlElements,
  elementHasClass,
  htmlCollectionToArray,
  addClassFromElements,
} from "./ui-scripts";
export const myWorkSpace = () => {
  const trigger = getHtmlElmentsByClass("-more-click");
  // this function triggers when user clicks on more buttom
  trigger.map((element) => {
    addEventListener(element, "click", () => {
      const parent = element.parentElement;
      if (parent.classList.contains("-expand")) {
        removeClassFromElements("c-workspace__more-wrap", "-expand");
        return false;
      }
      var el = document.querySelectorAll(".c-workspace__more-wrap");
      for (var i = 0; i < el.length; i++) {
        if (el[i].classList.contains("-expand")) {
          el[i].classList.remove("-expand");
        }
      }
      addClassToElement(parent, "-expand");
    });
  });

  window.addEventListener("click", (event) => {
    if (!event.target.classList.contains("icon-xdots-vertical")) {
      let el = document.querySelectorAll(".c-workspace__more-wrap");
      for (var i = 0; i < el.length; i++) {
        if (el[i].classList.contains("-expand")) {
          el[i].classList.remove("-expand");
        }
      }
    }
  });
};



