export const addFavourites = ()=>{
    const allFavourites = document.querySelectorAll('.c-favourite__fav-icon');
    if(allFavourites.length > 0){
        allFavourites.forEach((fav)=>{
            if(!fav.parentElement.classList.contains('-listenon')){
                fav.parentElement.classList.add('-listenon')
                function handleClick(el){
                    const parent = el.target.closest('.c-favourite')
                    if(parent.classList.contains('-active')){
                        parent.classList.remove('-active');
                    }else{
                        parent.classList.add('-active');
                    }
                }
                fav.addEventListener('click', handleClick,false);
            }
        })
    }
}


