import {
    addEventListener,
    getHtmlElmentsByClass,
    getElementById,
    removeClassFromElements,
    addClassToElement
  } from "./ui-scripts";
  
  export const successAlert = () => {
    /*Success Alert display*/
    
    const tick = getHtmlElmentsByClass("-success");
    const alert = getElementById("success-popup");
    const save = document.querySelector(".c-botcon__text .-success");
    tick.map((tab) => {
        addEventListener(tab, "click", () => {
            alert.style.display = "block";
            removeClassFromElements("-bottom-para", "-edit"); // disable textarea
            addClassToElement(save, "-disabled"); // disable the tick
        });
    });

    /*To close the success popup when trigger the close icon */
    const closeBtns = [].slice.call(
        document.querySelectorAll(".c-success-popup__close-button")
      );
      closeBtns.forEach(function (btn) {
        btn.onclick = function () {
          let modal = btn.closest(".c-success-popup");
          modal.style.display = "none";
        };
      });

    /*Edit trigger */
    const editTab = getHtmlElmentsByClass("icon-xedit-small");
    const textEdit = document.querySelector(".c-botcon__text .-bottom-para");
    editTab.map((tab) => {
        addEventListener(tab, "click", () => {
          removeClassFromElements("icon-xtick-small", "-disabled");
          addClassToElement(textEdit, "-edit");
        });
      });
  }


