import {
    addClassToElement,
    getHtmlElmentsByClass,
    removeClassFromElements,
    removeClassFromElement,
    addEventListener,
    elementHasClass,
  } from "./ui-scripts";

export const workspaceTemplate = () => {
  const allWorkspaceTemp = getHtmlElmentsByClass('c-sp-allworkspacetemp-table__exp-showmore');
  const workspaceoption = getHtmlElmentsByClass('-activate');
  const workspacelistoption = getHtmlElmentsByClass('-activated');
  workspacelistoption.map(template => {
    addEventListener(template, 'click', (el)=>{
      const parentContent = template.closest('.c-allworkspacetemp-table__row-wrapper');
      const toggle = parentContent.classList.contains('-active');
      if(toggle && parentContent.classList.contains('-active') && (template.firstElementChild.innerHTML === "Deactivate")){
        removeClassFromElement(parentContent, '-active');
        addClassToElement(parentContent, '-disabled');
        template.firstElementChild.innerHTML = "Activate";
      }else {
        removeClassFromElement(parentContent, '-disabled');
        addClassToElement(parentContent, '-active');
        template.firstElementChild.innerHTML = "Deactivate";
      }
    })
  })

  workspaceoption.map(template => {
    addEventListener(template, 'click', (el)=>{
      const parentContent = template.closest('.c-workspace-temp-new__listitem');
      const toggle = parentContent.classList.contains('-active');
      if(toggle && parentContent.classList.contains('-active') && (template.firstElementChild.innerHTML === "Deactivate")){
        removeClassFromElement(parentContent, '-active');
        addClassToElement(parentContent, '-disabled');
        template.firstElementChild.innerHTML = "Activate";
      }else {
        removeClassFromElement(parentContent, '-disabled');
        addClassToElement(parentContent, '-active');
        template.firstElementChild.innerHTML = "Deactivate";
      }
    })
  })
  allWorkspaceTemp.map(template => {
    addEventListener(template, 'click', (el)=>{
      const parentContent = template.closest('.c-sp-allworkspacetemp-table__row-wrapper');
      const toggle = !!parentContent.classList.contains('-showmore');
      removeClassFromElements('c-sp-allworkspacetemp-table__row-wrapper', '-showmore');
      allWorkspaceTemp.map(item => {
        item.innerHTML = "Show more";
      })
      
      if(!toggle && !parentContent.classList.contains('-showmore') && (el.target.innerHTML === "Show more")){
        parentContent.classList.add('-showmore');
        template.innerHTML = "Show less";
      }
    })
  })
};


