import {
  addEventListener
} from "./ui-scripts";

function dropdownShow() 
{ 
  var searchDropdowns = document.querySelectorAll(".o-search-box-dropdown__wrapper");
  searchDropdowns.forEach(searchDropdown => {
    var searchBox = searchDropdown.querySelector(".o-search-box-dropdown__input");
    addEventListener(searchBox, "keyup", () => {
      var searchbox_wrapper = searchBox.parentElement;
      searchbox_wrapper.querySelector(".o-search-box-dropdown__inner-srch-result").style.display = "block";
      var searchResults = searchbox_wrapper.querySelector(".c-srchfilter-content");
      searchResults && searchbox_wrapper.querySelector(".c-srchfilter-content").classList.add("-open");
      searchBox.classList.add("-search-open");
    });
  })
}

function dropdownHide()
{
  var searchDropdowns = document.querySelectorAll(".o-search-box-dropdown__wrapper");
  searchDropdowns.forEach(searchDropdown => {
    var closeIcon = searchDropdown.querySelector(".o-search-close");
    addEventListener(closeIcon, "click", () => {
      var searchbox_wrapper = closeIcon.parentElement.parentElement;
      searchbox_wrapper.querySelector(".o-search-box-dropdown__inner-srch-result").style.display = "none";
      var searchResults = searchbox_wrapper.querySelector(".c-srchfilter-content");
      searchResults && searchbox_wrapper.querySelector(".c-srchfilter-content").classList.remove("-open");
      var searchBox = searchbox_wrapper.querySelector(".o-search-box-dropdown__input");
      searchBox.classList.remove("-search-open");
    });
  })
}

export const dropdownToggle = () => {
  dropdownShow();
  dropdownHide();
}


