import {
  getElementById,
  addEventListener,
  elementHasClass,
  addClassToElement,
  removeClassFromElement,
  getChildElements,
  getHtmlElmentsByClass,
} from "./ui-scripts";

class SelectInput {
  constructor(htmlID, onSelect) {
    if (typeof htmlID === "string") {
      this.selectField = getElementById(htmlID);
    } else {
      this.selectField = htmlID;
    }

    if (this.selectField) {
      this.inputElement = this.selectField.firstElementChild;
      this.inputOptions = this.selectField.lastElementChild;
      this.onSelect = onSelect;
      this.init();
    }
  }

  showOptions() {
    const allDropdown = document.querySelectorAll('.c-select-wrapper');
    if(allDropdown.length > 0){
      allDropdown.forEach(element => {
        const allActive = element.querySelectorAll('.-active');
        if(allActive.length > 0) {
          allActive.forEach((el)=>{
            el.classList.remove('-active');
          })
        }
      });
    }
    const searchContainer = document.querySelector('.c-filter-suggestion__container');
    if(searchContainer){
      elementHasClass(searchContainer, '-active') &&
      removeClassFromElement(searchContainer, '-active');
    }

    addClassToElement(this.inputElement, "-active");
    addClassToElement(this.inputOptions, "-active");
    const addAssessId = this.selectField.closest('.c-modal');
    const kpiTable = this.inputOptions.closest('.c-create-kpi__form') || addAssessId.id.includes('add-assessment-modal');
    let targetEle = kpiTable;
    targetEle && (this.inputOptions.scrollIntoView({ behavior: "smooth", block: "center", inline: "center" }));  
}

  hideOptions() {
    removeClassFromElement(this.inputElement, "-active");
    removeClassFromElement(this.inputOptions, "-active");
  }

  addValueToInput(valueLabel, select) {
    addClassToElement(this.inputElement, "-input");
    if (select) {
      while (this.inputElement.childNodes.length > 1 && this.inputElement.hasChildNodes()) {
        this.inputElement.removeChild(this.inputElement.lastChild);
      }
      if (valueLabel.count) {
        let selectedText = this.inputElement.innerText;
        let noChange = !!select.find((el) => {
          if (el == this.inputElement.innerText) return true
        });
        if (!noChange || selectedText === "Select") {
          this.inputElement.children[0].innerText = select[0];
        }
        this.inputElement.append(valueLabel.element)
      } else {
        this.inputElement.children[0].innerText = select.length > 0 ? select[0] : "Select";
      }
    }
  }

  onElementClick() {
    addEventListener(this.inputElement, "click", () => {
      if (!elementHasClass(this.inputElement, "-active")) {
        this.showOptions();
      } else {
        this.hideOptions();
      }
    });
  }

  onOptionsClick() {
    const options = getChildElements(this.inputOptions);
    options.map((option) => {
      if(!option.classList.contains("c-dd-cbcount__options-listwrapper"))
      {
        addEventListener(option, "click", () => {
          let label = option.innerText;
          let value = option.getAttribute("data-value");
          this.addValueToInput(label);
          this.onSelect(label, value);
         // this.hideOptions();
        });
      }
    });
  }

  onCheckBoxClick() {
    const allChkBoxWrapper = this.inputOptions.querySelectorAll(".c-dd-cbcount__options-listwrapper");
    allChkBoxWrapper.forEach(chkBoxWrapper => {
      var options = chkBoxWrapper.childNodes;
      options.forEach((option) => {
        option.addEventListener("click", (element) => {
        if(elementHasClass(element.target,"c-dd-cbcount__eye-wrap") || elementHasClass(element.target,"c-dd-cbcount__eye")) {
          element.preventDefault();
          // location.href="https://www.google.com/";
          // return ;
        }

          let selectedCheckBox = chkBoxWrapper.querySelectorAll('input[name="multicheckbox"]:checked');
          let optionCheck = option.querySelector('input[name="multicheckbox"]:checked');
          let selectedOptions = [];
          selectedCheckBox.forEach((el) => {
            selectedOptions.push(el.parentElement.parentElement.innerText)
          })

          if (option.classList.contains("-selected") && !optionCheck) {
            removeClassFromElement(option, "-selected");
          } else {
            addClassToElement(option, "-selected");
          }
          if (selectedCheckBox.length > 0) {
            let countSelected = document.createElement("span");
            let text = "(".concat(selectedCheckBox.length).concat(")");
            const accDropdown = option.closest('.-accdropdown');
            if(accDropdown){
              text = "+".concat(selectedCheckBox.length);
            }
            countSelected.className = "c-dd-cbcount__count";
            countSelected.innerText = text.toString();
            var selectedTextObject = {
              count: true,
              element: countSelected
            };
            this.addValueToInput(selectedTextObject, selectedOptions);
            return;
          }
          this.addValueToInput({
            count: false
          }, selectedOptions);
        });
      });
    })
  }

  init() {
    this.onElementClick();
    this.onCheckBoxClick();
    this.onOptionsClick();
  }
}

export const dropdownCbCount = () => {
  const selectElements = getHtmlElmentsByClass("c-dd-cbcount");
  selectElements.map((element) => {
    new SelectInput(element, (label, value) => {});
  });

  window.SelectInput = SelectInput;
};


