export const bsGridOverlayJS = (cols=48) => {

  let gridActive = false;

  let colsTmpl = '';

  const body = document.querySelector("body");

  for(var i=0;i<cols;i+=1) {
    colsTmpl += '<div class="col-xs-1"><div class="fill"></div></div>';
  }

  let gridTmpl = `
  <div class="bs-grid-overlay">
    <div class="container">
      <div class="row">
        ${colsTmpl}
      </div>
    </div>
  </div>`;
  

  var node = document.createElement("div"); 
  
      

  document.addEventListener('keydown', (event) => {

    if(event.altKey  && event.keyCode == 71 && gridActive == false) {      
      node.innerHTML = gridTmpl;
      body.appendChild(node);      
      gridActive = true;
    }
    else {      
      node.innerHTML = '';
      gridActive = false;
    }

  }, false);

}



