import {
    getElementById,
    addEventListener,
    elementHasClass,
    addClassToElement,
    removeClassFromElement,
    getChildElements,
    getHtmlElmentsByClass,
  } from "./ui-scripts";
  class SelectInput {
    constructor(htmlID, onSelect) {
      if (typeof htmlID === "string") {
        this.selectField = getElementById(htmlID);
      } else {
        this.selectField = htmlID;
      }
  
      if (this.selectField) {
        this.inputElement = this.selectField.firstElementChild;
        this.inputOptions = this.selectField.lastElementChild;
        this.onSelect = onSelect;
        this.init();
      }
    }
  
    showOptions() {
      addClassToElement(this.inputElement, "-active");
      addClassToElement(this.inputOptions, "-active");
    }
  
    hideOptions() {
      removeClassFromElement(this.inputElement, "-active");
      removeClassFromElement(this.inputOptions, "-active");
    }
  
    addValueToInput(valueLabel) {
      addClassToElement(this.inputElement, "-input");
      this.inputElement.innerText = valueLabel;
    }
  
    onElementClick() {
      addEventListener(this.inputElement, "click", () => {
        if (!elementHasClass(this.inputElement, "-active")) {
          this.showOptions();
        } else {
          this.hideOptions();
        }
      });
    }
    onOptionsClick() {
      const options = getChildElements(this.inputOptions);
      options.map((option) => {
        addEventListener(option, "click", () => {
          const btndropdown = option.querySelector('.c-select__option-title');
          let label = option.innerText;
          if(btndropdown){
            label = btndropdown.innerText;
          }
          let value = option.getAttribute("data-value");
          this.addValueToInput(label);
          this.onSelect(label, value);
          this.hideOptions();
        });
      });
    }
  
    init() {
      this.onElementClick();
      this.onOptionsClick();
    }
  }
  
  export const selectElement = () => {
    const selectElements = getHtmlElmentsByClass("c-select");
  
    selectElements.map((element) => {
      new SelectInput(element, (label, value) => {});
    });
  
    window.SelectInput = SelectInput;
  };
  


