import {
    addEventListener,
    addClassToElement,
    removeClassFromElements,
    getHtmlElmentsByClass,
    getElementById,
  } from "./ui-scripts";

  const workspaceTabAction = () => {
    const twinTabs = getHtmlElmentsByClass("c-workspace-template__listitem");
    const requestWorkspace = getHtmlElmentsByClass("c-select-tool__client-requestbtn");
    twinTabs.map((tab) => {
      addEventListener(tab, "click", () => {
        const contentId = tab.getAttribute("data-show");
        const content = getElementById(contentId);
        if(content) {
          if(!content.classList.contains('-active')){
            content.classList.add('-active');
          }
        }
      });
    });
    requestWorkspace.map((tab) => {
      addEventListener(tab, "click", () => {
        const contentId = tab.getAttribute("data-show");
        const content = getElementById(contentId);
        if(content) {
          if(!content.classList.contains('-active')){
            content.classList.add('-active');
          }
        }
      });
    });
  };

  const allWorkspaceHandle = ()=>{
    const workspacHead = document.querySelector('.-workspaceall-head');
    const workspacCont = document.querySelector('.-workspaceall-cont');
    const workspacTab = document.querySelector('.-workspaceall-tab');

    if(workspacCont && workspacHead && workspacTab) {
      const template = workspacHead.querySelector('.c-select-tool__showmore');
      addEventListener(template, 'click', ()=> {
        if(workspacTab.classList.contains('-active')){
          const toggle = !!workspacCont.classList.contains('-active');
          if (!toggle && !workspacCont.classList.contains('-active') && (template.innerHTML === "Show more")) {
            workspacCont.classList.add('-active');
            template.innerHTML = "Show less";
          } else {
            removeClassFromElements('-workspaceall-cont', '-active');
            template.innerHTML = "Show more";
          }
        }
      })
    }
  }

  export const workspaceTab = () => {
    workspaceTabAction();
    allWorkspaceHandle();
  };
  


