import {
  getHtmlElmentsByClass,
  addEventListener,
  removeClassFromElements
} from './ui-scripts';

export const useThisTool = () => {

  const dcskpiTemp = getHtmlElmentsByClass('c-select-tool__showmore');
  dcskpiTemp.map(template => {
    addEventListener(template, 'click', (el) => {
      const parentContent = template.closest('.c-select-tool__wrap');
      const toggle = parentContent && !!parentContent.querySelector('.c-select-tool__grid').classList.contains('-active');
      removeClassFromElements('c-select-tool__grid', '-active');
      template.innerHTML = "Show more";

      if (!toggle && parentContent && !parentContent.querySelector('.c-select-tool__grid').classList.contains('-active') && (el.target.innerHTML === "Show more")) {
        parentContent.querySelector('.c-select-tool__grid').classList.add('-active');
        template.innerHTML = "Show less";
      }
    })
  })
}




