import { addClassToElement, addEventListener, elementHasClass, getElementById, getHtmlElmentsByClass, removeClassFromElement } from "./ui-scripts";

class FileInput {
    constructor() {
        this.init();
    }

    init() {
        let fileInputEle = getHtmlElmentsByClass('c-file-upload__input');
        fileInputEle && fileInputEle.forEach(inpElement => {
            const dropConatiner = inpElement.parentElement.parentElement.parentElement;
            const buttonClick = inpElement.parentElement;
            addEventListener(buttonClick, "click", () => {
                inpElement.click();
            });
            ['dragenter', 'dragover', 'dragleave', 'drop'].forEach(eventName => {
                dropConatiner.addEventListener(eventName, this.preventDefaults, false);
                eventName === 'drop' && dropConatiner.addEventListener(eventName, (e) => {
                    if (e.dataTransfer.files.length) {
                        inpElement.files = e.dataTransfer.files;
                        this.displayFileName(inpElement, inpElement.files);
                    }
                });
            });
            inpElement.addEventListener("change", (e) => {
                if (inpElement.files.length) {
                    this.displayFileName(inpElement, inpElement.files);
                }

            });
        });
    }

    preventDefaults(e) {
        e.preventDefault()
        e.stopPropagation()
    }

    displayFileName(inpElement, files) {
        const fileListAsArray = Array.from(files);
        let el = inpElement.parentElement.parentElement.parentElement.parentElement.lastElementChild;
        if (elementHasClass(el, 'c-file-upload__error')) {
            el = inpElement.parentElement.parentElement.parentElement.parentElement.lastElementChild.previousElementSibling;
        }
        fileListAsArray.forEach((element) => {
            if (element) {
                let fname = null;
                let nameWrap = el.cloneNode(true);
                var fileWrap = el.parentElement.firstElementChild;
                if (el && !elementHasClass(el, '-active') && !elementHasClass(el, '-error')) {
                    addClassToElement(el, '-active');
                    fname = el.querySelector('.c-file-upload__fname');
                    fname.innerHTML = element.name;

                } else {
                    addClassToElement(nameWrap, '-active');
                    removeClassFromElement(nameWrap, '-error')
                    fname = nameWrap.querySelector('.c-file-upload__fname');
                    fname.innerHTML = element.name;
                    this.insertAfter(fileWrap, nameWrap);
                }
                fname.nextElementSibling.innerHTML = 0 + '%';
                fname.nextElementSibling.nextElementSibling.firstElementChild.firstElementChild.style.width = 0 + '%';
                this.fileProgressBar(element, fname);
            }
        });

        this.removeUploadBtn(inpElement);
    }

    fileProgressBar(file, nameWrap) {
        const reader = new FileReader();

        reader.readAsDataURL(file);

        reader.addEventListener('progress', (event) => {
            if (event.loaded && event.total) {
                const percent = (event.loaded / event.total) * 100;
                this.showProgressBar(percent, nameWrap);
            }
        })
        reader.addEventListener('load', (event) => {
            const percent = (event.loaded / event.total) * 100;
            this.showProgressBar(percent, nameWrap);

        })
    }

    showProgressBar(percent, nameWrap) {
        setTimeout(() => {
            nameWrap.nextElementSibling.nextElementSibling.firstElementChild.firstElementChild.style.width = `${percent}%`;
            nameWrap.nextElementSibling.innerHTML = Math.round(percent) + '%';
        }, 0);
    }

    insertAfter(referenceNode, newNode) {
        referenceNode.parentNode.insertBefore(newNode, referenceNode.nextSibling);
    }

}
export const fileInput = () => {
    new FileInput();
};


