import {
    addEventListener,
    addClassToElement,
    removeClassFromElements,
    getHtmlElmentsByClass,
    getElementById,
  } from "./ui-scripts";

  const questionTabAction = () => {
    const twinTabs = getHtmlElmentsByClass("c-vertical-tabs__link");
    if(twinTabs.length>0)
    {
    twinTabs.map((tab) => {
      addEventListener(tab, "click", () => {
        const contentId = tab.getAttribute("data-show");
        const content = getElementById(contentId);
        if(content) {
          removeClassFromElements("c-vertical-tabs__link", "-is-active");
          removeClassFromElements("c-vertical-tabs__content", "-is-active");
          addClassToElement(tab, "-is-active");
          addClassToElement(content, "-is-active");
        }
      });
    });
  }
  };

  export const questionnarieTabAction = () => {
    questionTabAction();
  };
  


