import { getHtmlElmentsByClass,
    addEventListener,
    removeClassFromElement,
    addClassToElement,
    elementHasClass, } from "./ui-scripts";
/* Custom popup starts from here */
export const customPopup = () => {
    const selectedlabel = getHtmlElmentsByClass("c-popup__p-js");
    selectedlabel.map((element, ind) => {
      addEventListener(element, "click", () => {
        const parentElement = element.parentElement.parentElement;
        if (elementHasClass(parentElement, "-active")) {
          removeClassFromElement(parentElement, "-active");
        } else {
          addClassToElement(parentElement, "-active");
        }
      });
    });
}
/* Custom popup ends from here */


