import {
  addClassToElement,
  getHtmlElmentsByClass,
  removeClassFromElements,
  addEventListener,
  elementHasClass,
  htmlCollectionToArray
} from "./ui-scripts";

export const solutionTabs = () => {
  var solTabContainer = document.querySelector(".c-solution__tabs");
  var tabTriggers = getHtmlElmentsByClass("c-solution__tabs-label");
  tabTriggers.map((tab) => {
    tab.addEventListener("click", function() {
      // var tabContent = accTabContainer.querySelector(".[tab-content='"+tab.getAttribute("tab-trigger")+"']");
      removeClassFromElements("c-solution__tabs-label", "-active");
      addClassToElement(tab, "-active");
    })
  })
}


