import {
    addEventListener,
    addClassToElement,
    removeClassFromElements,
    getHtmlElmentsByClass,
    getElementById,
  } from "./ui-scripts";

  const horizontalTabAction = () => {
    const twinTabs = getHtmlElmentsByClass("c-horizontal-tabs__item");
    twinTabs.map((tab) => {
      addEventListener(tab, "click", () => {
        const workspacHead = document.querySelector('.-workspaceall-head');
        const contentId = tab.getAttribute("data-show");
        const content = getElementById(contentId);
        if(content) {
          const tabParent = tab.closest('.c-horizontal-tabs');
          const allItem = tabParent.querySelectorAll('.c-horizontal-tabs__item');
          const allContent = tabParent.querySelectorAll('.c-horizontal-tabs__content');
          allItem && allItem.forEach(item => {
            item.classList.remove('-active');
          })
          allContent && allContent.forEach(item => {
            item.classList.remove('-active');
          })
          addClassToElement(tab, "-active");
          addClassToElement(content, "-active");
          // to handle tile leaf icon
          // let hasSolTile = content.querySelector("#solution-tile");
          // hasSolTile && solutionTileTxt();

          if(tab.classList.contains('-workspaceall-tab')){
            workspacHead && (workspacHead.previousElementSibling.style.display = 'none');
            workspacHead && (workspacHead.style.display = 'flex');
          } else {
            workspacHead && workspacHead.previousElementSibling.removeAttribute('style');
            workspacHead && workspacHead.removeAttribute('style');
          }
        }
      });
    });
  };

  export const horizontalTab = () => {
    horizontalTabAction();
  };
  


