import {
    addEventListener,
    addClassToElement,
    removeClassFromElements,
    getHtmlElmentsByClass,
    getElementById,
  } from "./ui-scripts";

  const clientTabActions = () => {
    const twinTabs = getHtmlElmentsByClass("c-select-tool__ctile-wrap");
    twinTabs.map((tab) => {
      addEventListener(tab, "click", () => {
        const contentId = tab.getAttribute("data-show");
        const content = getElementById(contentId);
        if(content) {
          removeClassFromElements("c-select-tool__ctile-wrap", "-active");
          removeClassFromElements("c-select-tool__workspace", "-active");
          addClassToElement(tab, "-active");
          addClassToElement(content, "-active");
        }
      });
    });
  };

  export const clientTab = () => {
    clientTabActions();
  };
  


