import {
    getHtmlElmentsByClass,
    addEventListener,
    addClassToElement,
    removeClassFromElement,
    removeClassFromElements,
    getQueryHtmlElements,
    elementHasClass,
    htmlCollectionToArray, 
    addClassFromElements
} from './ui-scripts';

export const spfavourite = () => {
     const action = document.querySelector(".c-solution-header__mark");
        addEventListener(action, "click", () => {
            let heart=action.querySelector('.icon-favourite-no-fill')
            let hearttxt=action.querySelector('.c-solution-header__label')

         if(elementHasClass(heart,'-active'))
           {
              removeClassFromElement(heart,'-active')
              hearttxt.innerText="Remove from Favourites" ;
           }
           else
           {
               addClassToElement(heart,'-active')
               hearttxt.innerText="Mark as Favourite" ;
           }
        })
}


