import {
    getHtmlElmentsByClass,
    addEventListener,
    addClassToElement,
    removeClassFromElement,
    removeClassFromElements,
    getQueryHtmlElements,
    elementHasClass,
    htmlCollectionToArray,
    addClassFromElements
} from './ui-scripts';

export const solDetails = () => {
    const action = getHtmlElmentsByClass("c-solution-overview__arrow");
    action.map((item, index) => {
        addEventListener(item, "click", () => {
            const popup = item.parentElement;
            popup.classList.toggle("-show");
        })
    });
    const parentTool = getHtmlElmentsByClass("c-solution-header__icon-top-parent");
    parentTool.map((tab) => {
        addEventListener(tab, "click", () => {
            const mainDiv = tab.closest(".c-solution-header__parent-icon");
            const tooltip = mainDiv.querySelector('.c-dropdown-tooltip');
            if (!elementHasClass(tab, "-active")) {
                addClassToElement(tab, "-active");
                addClassToElement(tooltip, "-active");
            } else {
                removeClassFromElement(tab, "-active");
                removeClassFromElement(tooltip, "-active");
            }
        });
    });
}




