export const gridListview = () => {
    const gridView = [...document.querySelectorAll('.c-sp-filters__grid-icon')];
    const listView = [...document.querySelectorAll('.c-sp-filters__list-icon')];

    gridView && gridView.forEach((gView, index) => {
        gView.addEventListener('click', (el) => {
            if (!gView.classList.contains('-active')) {
                gView.classList.add('-active')
            }
            if (listView[index].classList.contains('-active')) {
                listView[index].classList.remove('-active')
            }
            const tilegridContainer = document.querySelectorAll('.c-sp-filters__content-tiles');
            const tilelistContainer = document.querySelectorAll('.c-sp-filters__content-list');
            if (tilegridContainer.length > 0) {
                if (tilegridContainer[index] && !tilegridContainer[index].classList.contains('-active')) {
                    tilegridContainer[index].classList.add('-active');
                }

            }
            if (tilelistContainer.length > 0) {
                if (tilelistContainer[index] && tilelistContainer[index].classList.contains('-active')) {
                    tilelistContainer[index].classList.remove('-active');
                }

            }
        });
    });


    listView && listView.forEach((lView, index) => {
        lView.addEventListener('click', (el) => {
            if (!lView.classList.contains('-active')) {
                lView.classList.add('-active');
            }
            if (gridView[index].classList.contains('-active')) {
                gridView[index].classList.remove('-active');
            }
            const tilegridContainer = document.querySelectorAll('.c-sp-filters__content-tiles');
            const tilelistContainer = document.querySelectorAll('.c-sp-filters__content-list');
            if (tilegridContainer.length > 0) {
                // console.log(tilegridContainer.length);
                if (tilegridContainer[index] && tilegridContainer[index].classList.contains('-active')) {
                    tilegridContainer[index].classList.remove('-active');
                }

            }
            if (tilelistContainer.length > 0) {
                // console.log(tilegridContainer.length);
                if (tilelistContainer[index] && !tilelistContainer[index].classList.contains('-active')) {
                    tilelistContainer[index].classList.add('-active');
                }
            }
        });
    });
}


