// for modal carousels: 
// Optimization Carousel
const optimization_carousel = new SolutionCarousel("optimization-carousel", {
  items: 1,
  firstItemFixed: false,
  dots: true,
  arrows: true,
  leftArrow: `<i class="icon-arrow-left"></i>`,
  rightArrow: `<i class="icon-arrow-left"></i>`,
  margin: 20,
});

// Preview Carousel
const preview_carousel = new SolutionCarousel("preview-carousel", {
  items: 1,
  firstItemFixed: false,
  dots: true,
  arrows: true,
  leftArrow: `<i class="icon-arrow-left"></i>`,
  rightArrow: `<i class="icon-arrow-left"></i>`,
  margin: 20,
})

export const modalCarouselList = [
  {
    "id": "optimization-carousel",
    "object": optimization_carousel
  },
  {
    "id": "preview-carousel",
    "object": preview_carousel
  },
]



