import {
  addEventListener,
  getHtmlElmentsByClass,
  addClassToElement,
  removeClassFromElement,
  elementHasClass,
} from "./ui-scripts";

export const accordionFn = () => {
  const collapseTabs = getHtmlElmentsByClass(
    "c-question-accordion__collapse-head"
  );

  let accItem = document.getElementsByClassName('c-question-accordion__collapse-item');
  let accHD = document.getElementsByClassName('c-question-accordion__collapse-head');
  let i;
  for (i = 0; i < accHD.length; i++) {
    accHD[i].addEventListener('click', toggleItem, false);
  }
  /* Accordion open close button funcionality*/
  function toggleItem() {
    let itemClass = this.parentNode.className;
    for (i = 0; i < accItem.length; i++) {
      accItem[i].className = 'c-question-accordion__collapse-item -close';
    }
    if (itemClass == 'c-question-accordion__collapse-item -close') {
      this.parentNode.className = 'c-question-accordion__collapse-item -open';
   }
  }
  collapseTabs.map((tab) => {
    addEventListener(tab, "click", () => {
      const parent = tab.closest(".c-question-accordion__collapse-item");
      if (!elementHasClass(parent, "-close")) {
         addClassToElement(parent, "-open");
        addClassToElement(parent, "-is-glow")

      } else {
        removeClassFromElement(parent, "-open");
        removeClassFromElement(parent, "-is-glow");
      }
      


    });
  });
  // const collapseinnerTabs = getHtmlElmentsByClass(
  //   "c-question-accordion__innercollapse-head"
  // );
  let accinnerItem = document.getElementsByClassName('c-question-accordion__innercollapse-item');
  let accinnerHD = document.getElementsByClassName('c-question-accordion__innercollapse-head');
  let k;
  for (k = 0; k < accinnerHD.length; k++) {
    accinnerHD[k].addEventListener('click', toggleItem_inner, false);
  }
  /* Accordion open close button funcionality*/
  function toggleItem_inner() {
    let itemClass = this.parentNode.className;
    for (i = 0; i < accinnerItem.length; i++) {
      accinnerItem[i].className = 'c-question-accordion__innercollapse-item -closeinner';
    }
    if (itemClass == 'c-question-accordion__innercollapse-item -closeinner') {
      this.parentNode.className = 'c-question-accordion__innercollapse-item -openinner';
   }
  }

  // collapseinnerTabs.map((tab) => {
  //   addEventListener(tab, "click", () => {
  //     const inner = tab.closest(".c-question-accordion__innercollapse-item");
  //     if (!elementHasClass(inner, "-closeinner")) 
  //     {
  //       addClassToElement(inner, "-openinner");
  //     } else {
  //       removeClassFromElement(inner, "-openinner");
  //       // removeClassFromElement(inner, "-closeinner");
  //     }
  //   });
  // });
};



