HTMLElement.prototype.wrapAll = function(elms) {
  var el = elms.length ? elms[0] : elms;
  var parent  = el.parentNode;
  var sibling = el.nextSibling;
  this.appendChild(el);
  while (elms.length) {
      this.appendChild(elms[0]);
  }
  if (sibling) {
      parent.insertBefore(this, sibling);
  } else {
      parent.appendChild(this);
  }
};

class scrollableTabs {
  constructor(htmlNode)
  {
    this.progenitorNode = htmlNode;
    this.options = {
      scrollSpeedDiv: 2, //decrease for increase in scroll step-distance
      scrollSpeedStep: 200
    }
    this.state = {
      containerTranslation: 0,
      wrapperWidth: null,
    }
    this.setup();
    this.bindEvents();
  }

  setup() {
    this.tabsContainer = this.progenitorNode.querySelector(".c-scrollable-tabs__container");
    this.tabsContainer.style.position = "relative";
    this.tabsContWrapper = document.createElement("div");
    this.tabsContWrapper.className = "c-scrollable-tabs__container-wrapper";
    this.tabsContWrapper.wrapAll(this.tabsContainer);
    this.tabs = this.tabsContainer.querySelectorAll(".c-scrollable-tabs__element");
    this.state.wrapperWidth = this.tabsContWrapper.offsetWidth;
    this.insertArrows();
    // this.selectTab(this.tabs[0]); //default tab
  }

  bindEvents() {
    //tab click event
    this.tabs.forEach((tab) => {
      tab.addEventListener("click", () => {
        this.selectTab(tab);
      });
    });

    //mousewheel event on container 
    this.tabsContainer.addEventListener("wheel", (ev) => {
      ev.preventDefault();
      let wd = ev.wheelDelta;
      this.setContainerTranslation({ wheelDelta: wd });
    });

    //arrow click events
    this.arrowLeft.addEventListener("click", () => {
      this.setContainerTranslation({ wheelDelta: this.options.scrollSpeedStep });
    });

    this.arrowRight.addEventListener("click", () => {
      this.setContainerTranslation({ wheelDelta: -this.options.scrollSpeedStep });
    });

    //resize event
    // window.addEventListener("resize", () => {
    //   this.init();
    //   this.setContainerTranslation({ wheelDelta: 0 });
    // });

    const wrapperResObs = new ResizeObserver((entries) => {
      entries.forEach((entry) => {
        this.init();
        this.setContainerTranslation({ wheelDelta: 0 });
      });
    });
    wrapperResObs.observe(this.tabsContWrapper);
    wrapperResObs.observe(this.tabsContainer);
  }

  init() {
    this.state.wrapperWidth = this.tabsContWrapper.offsetWidth;
    this.manageArrows();
  }

  insertArrows() {
    this.arrowLeft = document.createElement("span");
    this.arrowLeft.className = "c-scrollable-tabs__arrow";
    this.arrowLeft.classList.add("icon-arrow-mini-left");
    this.arrowLeft.classList.add("-left");

    this.arrowRight = document.createElement("span");
    this.arrowRight.className = "c-scrollable-tabs__arrow";
    this.arrowRight.classList.add("icon-arrow-mini-right");
    this.arrowRight.classList.add("-right");

    this.progenitorNode.prepend(this.arrowLeft);
    this.progenitorNode.append(this.arrowRight);
  }

  manageArrows() {
    const widthDelta = this.tabsContWrapper.offsetWidth < this.tabsContainer.offsetWidth;
    this.changeArrowsVisibility({ left: widthDelta, right: widthDelta });
  }

  changeArrowsVisibility({left, right}) {
    if(this.tabsContWrapper.offsetWidth >= this.tabsContainer.offsetWidth-1)
    {
      left !== undefined && this.arrowLeft.classList.toggle("-hidden", true);
      right !== undefined && this.arrowRight.classList.toggle("-hidden", true);
      // this.progenitorNode.style.padding = "0";
      return;
    }
    // this.progenitorNode.style.removeProperty("padding");
    left !== undefined && this.arrowLeft.classList.toggle("-hidden", false);
    right !== undefined && this.arrowRight.classList.toggle("-hidden", false);
    left !== undefined && this.arrowLeft.classList.toggle("-disabled", !left);
    right !== undefined && this.arrowRight.classList.toggle("-disabled", !right);
  }

  selectTab(tab)
  {
    //?

    //put the tab in the center / near to center if space is ample
    this.centerToTab(tab);
  }

  centerToTab(tab)
  {
    const wrapperWidth = this.tabsContWrapper.offsetWidth;
    const tabWidth = tab.offsetWidth;
    const spaceToCenter = Math.ceil((wrapperWidth - tabWidth)/2);
    const distFromParent = tab.offsetLeft;
    const translation = distFromParent - spaceToCenter;
    this.setContainerTranslation({value: -translation});
  }

  setContainerTranslation({wheelDelta, value})
  {
    const { containerTranslation, wrapperWidth } = this.state;
    var translationVal = value || containerTranslation + Math.floor(wheelDelta / this.options.scrollSpeedDiv);
    const widthLeftBound = Math.min(0, translationVal);
    const widthLeftAndRightBound = Math.max(
      wrapperWidth - this.tabsContainer.offsetWidth,
      widthLeftBound
    );
    this.state.containerTranslation = widthLeftAndRightBound;
    this.tabsContainer.style.transform = `translateX(${widthLeftAndRightBound}px)`;
    
    //toggle arrow visibility
    this.changeArrowsVisibility({
      left: widthLeftAndRightBound < 0,
      right: Math.abs(widthLeftAndRightBound) + wrapperWidth < this.tabsContainer.offsetWidth
    });
  }
}

export const scrollTabsInit = () => {
  document.querySelectorAll(".c-scrollable-tabs").forEach(scCont => {
    new scrollableTabs(scCont);
  });
}

window.scrollTabsInit = scrollTabsInit;


